

export function addText(svg, data) {

    const ydistance = 160;
    const disOffset = 35;
    const xdistance = 600;

    svg.selectAll('rect')
    .data([null])
    .enter().append('rect')
    .attr('y', 90)
    .attr('x', 580)
    .attr('width', 340)
    .attr('height', 300)
    .attr("stroke", "black")
    .attr('rx', 120 / 3)
    .style('fill', '#b9becf')
    .style('opacity', 0.3);

      svg
      .append('text')
      .attr('y', ydistance - 10)
      .attr('x', xdistance + 85)
      .attr('class', 'summaryTextTitle')
      .text("SUMMARY");

      svg
      .append('text')
      .attr('y', ydistance + disOffset)
      .attr('x', xdistance)
      .attr('class', 'summaryText')
      .text("Character: " + data[1].agent);

      svg
      .append('text')
      .attr('y', ydistance + disOffset * 2)
      .attr('x', xdistance)
      .attr('class', 'summaryText')
      .text("Map: " + data[1].map);

      svg
      .append('text')
      .attr('y', ydistance + disOffset * 3)
      .attr('x', xdistance)
      .attr('class', 'summaryText')
      .text("Total Credit Used: " + d3.sum(data, d => d.credited_used));

      svg
      .append('text')
      .attr('y', ydistance + disOffset * 4)
      .attr('x', xdistance)
      .attr('class', 'summaryText')
      .text("Total Credit Gathered: " + d3.sum(data, d => d.credit_gathered));




};






