import {
   select,
   csv,
   scaleLinear,
   scaleTime,
   extent,
   axisLeft,
   axisBottom,
   sliderBottom
 
 } from 'd3';

export const piePlot = (selection, props) =>{
   const {
        svg,
        title,
        data,
        radius,
        width,
        height,
        margin,
        test
        
   } = props;


   const g = svg.append('g')
  .attr('transform', `translate(${320},${320})`);
   
   const color = d3
    .scaleSequential(d3.interpolateYlOrRd)
    .domain([0, d3.max(data, function (d) { return d[test] })])
    .interpolator(d3.interpolate("#f1eef6", "#045a8d"));
     
    //a limit to round number
    const LIMITE = 11;

    function donnee() {
      var others = 0;
      let new_list = new Array();
      for (var pas = 0; pas < data.length; pas++) {
        if (pas < LIMITE) {
          var langue = data[pas].round;
          var valeur = data[pas][test];
          new_list.push({
            round: langue,
            [test]: valeur,
          });
        } else {
          others += data[pas][test];
        }
      }
      return new_list;
    }

    
  const pie = d3
  .pie()
  .sort(null) // Do not sort group by size
  .value((d) => +d[test]);
const data_ready = pie(donnee(data));

//This is the size of the donut hole
const arc = d3.arc()
  .innerRadius(radius * 0.5)         
  .outerRadius(radius * 0.8)

const outerArc = d3.arc()
  .innerRadius(radius * 0.9)
  .outerRadius(radius * 0.9);



const arcLabel = function () {
  const radius1 = Math.min(width, height) / 2 * 0.7;
  return d3.arc().innerRadius(radius1).outerRadius(radius1);
}

const tooltip = d3
  .select('body')
  .append('div')
  .style('position', 'absolute')
  .style('z-index', '10')
  .style('visibility', 'hidden')
  .style('padding', '8px')
  .style('background', 'rgba(0,0,0,0.6)')
  .style('border-radius', '4px')
  .style('color', '#fff');

svg
  .selectAll('path')
  .data(data_ready)
  .join("path")
  .attr("d", arc)
  .attr("fill", d => color(d.data[test]))
  .attr("stroke", "white")
  .style("stroke-width", "1.5px")
  .on('mouseover', function (d, i) {
    d3.select(this).transition()
      .duration('50')
      .attr('fill', d => color(d.data[test]))
      .attr('opacity', 0.6);

    

    tooltip
      .text(
        `Round: ${d.data.round}  ` +

        `Credit Used: ${d.data[test]}`

      )
      .style('visibility', 'visible');

  })
  .on('mousemove', (event, datum) => {
    //Be careful to use event.pageY in different D3 version
    tooltip
      .style('top', d3.event.pageY - 50 + 'px')
      .style('left', d3.event.pageX - 50 + 'px');
  })

  .on('mouseout', function (d, i) {
    d3.select(this).transition()
      .duration('50')
      .attr('opacity', 1);

    tooltip.style('visibility', 'hidden');
  });

 svg.append('text')
  .attr('class', 'title')
  .attr('y', -250)
  .attr('x', -125)
  .text(title); 
  

svg
  .selectAll('allPolylines')
  .data(data_ready)
  .enter()
  .append('polyline')
  .attr("stroke", "black")
  .style("fill", "none")
  .attr("stroke-width", 1)
  .attr('points', function (d) {
    var posA = arc.centroid(d) // line insertion in the slice
    var posB = outerArc.centroid(d) // line break: we use the other arc generator that has been built only for that
    var posC = outerArc.centroid(d); // Label position = almost the same as posB
    var midangle = d.startAngle + (d.endAngle - d.startAngle) / 2 // we need the angle to see if the X position will be at the extreme right or extreme left
    posC[0] = radius * 0.95 * (midangle < Math.PI ? 1 : -1); // multiply by 1 or -1 to put it on the right or on the left
    return [posA, posB, posC]
  })

const sum = d3.sum(data, d => d[test]);

svg.append("svg:image")
  .attr("xlink:href", './image/b.png')
  .attr("class", 'back_svg')
  .attr("width", 200)
  .attr("height", 190)
  .attr('y', -70)
  .attr('x', -100);

svg
  .selectAll('allLabels')
  .data(data_ready)
  .enter()
  .append('text')
  .text(function (d) {
   
    return (d3.format(".2%")(d.data[test] / sum));
  })
  .attr('transform', function (d) {
    var pos = outerArc.centroid(d);
    var midangle =
      d.startAngle +
      (d.endAngle - d.startAngle) / 2;
    pos[0] =
      radius *
      0.99 *
      (midangle < Math.PI ? 1 : -1);
    return 'translate(' + pos + ')';
  })
  .style('text-anchor', function (d) {
    var midangle =
      d.startAngle +
      (d.endAngle - d.startAngle) / 2;
    return midangle < Math.PI ? 'start' : 'end';
  });



}