//pie chart
import { addText } from './text';
import { piePlot } from './piePlot';

import {
  select,
  csv,
  scaleLinear,
  scaleTime,
  extent,
  axisLeft,
  axisBottom,
  sliderBottom

} from 'd3';

const svg = select('svg');
const width = +svg.attr('width');
const height = +svg.attr('height');

svg
  .attr('width', width)
  .attr('height', height)
  .append('rect')
  .attr('width', width)
  .attr('height', height)
  .attr('class', 'back_svg')
  .attr('fill', "#F4F6F6")
  .attr('rx', 40);


const render = data => {

  const margin = { top: 60, right: 40, bottom: 88, left: 105 }
  const innerWidth = width - margin.left - margin.right;
  const innerHeight = height - margin.top - margin.bottom;
  const radius = Math.min(width, height) / 2.5;

  var select = svg.append('g')
    .attr('transform', `translate(${320},${320})`);

  //refresh(select);
  select
    .append('g')
    .append('rect')
    .attr('y', 230)
    .attr('x', -120)
    .attr('class', 'usedButton')
    .attr('width', 100)
    .attr('height', 35)
    .style('fill', 'black')
    .attr("stroke", "#b9becf")
    .attr('opacity', 0.2);

  svg.call(piePlot, {
    svg: select,
    title: 'Total Credit Used',
    data: data,
    radius: radius,
    width: width,
    height: height,
    margin: margin,
    test: 'credited_used'

  });

  select
    .append('g')
    .append('rect')
    .attr('y', 230)
    .attr('x', -120)
    .attr('class', 'sele')
    .attr('width', 100)
    .attr('height', 35)
    .style('fill', 'white')
    .attr("stroke", "#2c4d82")
    .on('mouseover', function (d, i) {
      d3.select(this).transition()
        .duration('50')
        .attr('fill', 'black')
        .attr('opacity', 0.6);
    })
    .on('mouseout', function (d, i) {
      d3.select(this).transition()
        .duration('50')
        .attr('opacity', 1);
    })
    .on('click', function () {

      refresh(select);
      select.selectAll('rect.gatheredButton').remove();

      select
        .append('g')
        .append('rect')
        .attr('y', 230)
        .attr('x', -120)
        .attr('class', 'usedButton')
        .attr('width', 100)
        .attr('height', 35)
        .style('fill', 'black')
        .attr("stroke", "#b9becf")
        .attr('opacity', 0.2);

      svg.call(piePlot, {
        svg: select,
        title: 'Total Credit Used',
        data: data,
        radius: radius,
        width: width,
        height: height,
        margin: margin,
        test: 'credited_used'

      });


    });

  select
    .append('g')
    .append('rect')
    .attr('y', 230)
    .attr('x', 0)
    .attr('class', 'sele')
    .attr('width', 100)
    .attr('height', 35)
    .style('fill', 'white')
    .attr("stroke", "#2c4d82")
    .on('mouseover', function (d, i) {
      d3.select(this).transition()
        .duration('50')
        .attr('fill', 'black')
        .attr('opacity', 0.6);
    })
    .on('mouseout', function (d, i) {
      d3.select(this).transition()
        .duration('50')
        .attr('opacity', 1);
    })
    .on('click', function () {
      refresh(select);
      select.selectAll('rect.usedButton').remove();

      select
        .append('g')
        .append('rect')
        .attr('y', 230)
        .attr('x', 0)
        .attr('class', 'gatheredButton')
        .attr('width', 100)
        .attr('height', 35)
        .style('fill', 'black')
        .attr("stroke", "##b9becf")
        .attr('opacity', 0.2);


      svg.call(piePlot, {
        svg: select,
        title: 'Total Credit Gathered',
        data: data,
        radius: radius,
        width: width,
        height: height,
        margin: margin,
        test: 'credit_gathered'

      });


    });

  svg
    .append('text')
    .attr('y', 575)
    .attr('x', 220)
    .attr('class', 'uptext')
    .text("Used");

  svg
    .append('text')
    .attr('y', 575)
    .attr('x', 325)
    .attr('class', 'uptext')
    .text("Gathered");


  const text = svg.append('g')
    .attr('transform', ("translate(" + (margin.left) + "," + (margin.top) + ")"));

  addText(text, data);


  function refresh(svg) {

    svg.selectAll('path').remove();
    svg.selectAll('text').remove();
    svg.selectAll('polyline').remove();
    svg.selectAll('image').remove();


  }

};

const data = fetch('./data.json')
  .then(function (response) { return response.json(); })
  .then(function (data) {


    console.log(data);
    render(data);


  });